@import "../../styles/variables";
@import "../../styles/colors";
@import "../../styles/responsive";

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(239, 93, 168, 0.06);
  padding: 5% 25%;
  text-align: center;
  line-height: 3rem;

  .subTxt {
    color: #60606F;
    font-size: 1.1rem;
    margin-top: 3.5%;
    margin-bottom: 2%;
  }

  @include mobile {
    padding: 5% 10%;
    line-height: 2rem;
    .subTxt { 
      font-size: 0.7rem;
    }
    h1 {
      font-size: 1rem;
    }
  }
}
